import spectrumCanvas from './canvas'
import audioSpectrum from './audio'

export default class {
  private _canvas: spectrumCanvas
  private _analyser: audioSpectrum
  constructor() {
    this._analyser = new audioSpectrum()
    this._canvas = new spectrumCanvas()
  }

  async loadAudio(audioData: ArrayBuffer): Promise<void> {
    try {
      await this._analyser.decodeAudio(audioData)
      console.log('decodeAudio')
    } catch (error) {
      console.log(error)
    } finally {
      return
    }
  }
  start() {
    this._analyser.start()
    this._canvas.animationStart(this._analyser)
  }
  stop() {
    this._analyser.stop()
    this._canvas.animationStop()
  }
}
